* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-color: #2563eb;
  --primary-hover: #1d4ed8;
  --secondary-color: #64748b;
  --success-color: #10b981;
  --error-color: #ef4444;
  --warning-color: #f59e0b;
  --background-color: #ffffff;
  --surface-color: #f8fafc;
  --text-primary: #1e293b;
  --text-secondary: #64748b;
  --border-color: #e2e8f0;
  --shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --border-radius: 8px;
  --transition: all 0.3s ease;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
  line-height: 1.6;
  color: var(--text-primary);
  background-color: var(--background-color);
}

/* Skip Link for Accessibility */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: var(--primary-color);
  color: white;
  padding: 8px;
  text-decoration: none;
  border-radius: var(--border-radius);
  z-index: 1000;
}

.skip-link:focus {
  top: 6px;
}

/* Navigation */
.navbar {
  background: var(--background-color);
  border-bottom: 1px solid var(--border-color);
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: var(--shadow);
}

.nav-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 64px;
}

.nav-brand h1 {
  color: var(--primary-color);
  font-size: 1.5rem;
  font-weight: 700;
}

.nav-menu {
  display: flex;
  list-style: none;
  gap: 2rem;
}

.nav-link {
  text-decoration: none;
  color: var(--text-secondary);
  font-weight: 500;
  transition: var(--transition);
  padding: 0.5rem 0;
  border-bottom: 2px solid transparent;
}

.nav-link:hover,
.nav-link.active {
  color: var(--primary-color);
  border-bottom-color: var(--primary-color);
}

.nav-auth {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.nav-toggle {
  display: none;
  background: none;
  border: none;
  font-size: 1.5rem;
  color: var(--text-primary);
  cursor: pointer;
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: var(--border-radius);
  font-weight: 500;
  text-decoration: none;
  cursor: pointer;
  transition: var(--transition);
  font-size: 0.875rem;
}

.btn-primary {
  background: var(--primary-color);
  color: white;
}

.btn-primary:hover {
  background: var(--primary-hover);
}

.btn-outline {
  background: transparent;
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.btn-outline:hover {
  background: var(--primary-color);
  color: white;
}

.btn-full {
  width: 100%;
  justify-content: center;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-sm {
  padding: 0.4rem 0.8rem;
  font-size: 0.8rem;
}

/* Subscribe Button */
.subscribe-btn.subscribed {
  background-color: var(--success-color);
  color: white;
  border-color: var(--success-color);
}

.subscribe-btn.subscribed:hover {
  background-color: #0e9f6e; /* Darker green */
}

/* Main Content */
.main-content {
  min-height: calc(100vh - 64px);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 1rem;
}

.page {
  display: none;
}

.page.active {
  display: block;
}

.page-header {
  text-align: center;
  margin-bottom: 3rem;
}

.page-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: var(--text-primary);
}

.page-header p {
  font-size: 1.125rem;
  color: var(--text-secondary);
  max-width: 600px;
  margin: 0 auto;
}

/* Loading Spinner */
.loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid var(--border-color);
  border-top: 4px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Stories Grid */
.stories-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.story-card {
  background: var(--background-color);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  overflow: hidden;
  transition: var(--transition);
  display: flex;
  flex-direction: column;
}

.story-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
}

.story-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.story-content {
  padding: 1.5rem;
  flex-grow: 1; /* Allow content to grow */
  display: flex;
  flex-direction: column;
}

.story-author {
  font-weight: 600;
  color: var(--primary-color);
  margin-bottom: 0.5rem;
}

.story-description {
  color: var(--text-secondary);
  margin-bottom: 1rem;
  line-height: 1.5;
  flex-grow: 1; /* Allow description to grow */
}

.story-date {
  font-size: 0.875rem;
  color: var(--text-secondary);
}

.story-location {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.875rem;
  color: var(--text-secondary);
  margin-top: 0.5rem;
}

.card-actions {
  margin-top: 1rem;
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
  justify-content: center;
}

/* Story Detail Page */
.story-detail-content {
  max-width: 800px;
  margin: 0 auto;
}

.story-detail-card {
  background: var(--background-color);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-lg);
  overflow: hidden;
}

.story-detail-image {
  width: 100%;
  height: 400px;
  object-fit: cover;
}

.story-detail-body {
  padding: 2rem;
}

.story-detail-description {
  color: var(--text-primary);
  line-height: 1.8;
  margin-bottom: 1.5rem;
  white-space: pre-wrap; /* Preserve whitespace and line breaks */
}

.story-detail-location {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1rem;
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
}

.detail-map {
  height: 400px;
  width: 100%;
  border-radius: var(--border-radius);
  overflow: hidden;
  margin-top: 1.5rem;
  border: 1px solid var(--border-color);
}

.bookmark-btn {
  margin-top: 1.5rem;
  width: auto;
  justify-content: center;
}

.bookmark-btn.bookmarked {
  background-color: var(--primary-color);
  color: white;
}

.bookmark-btn.bookmarked:hover {
  background-color: var(--primary-hover);
}

/* Forms */
.story-form,
.auth-form {
  max-width: 600px;
  margin: 0 auto;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: var(--text-primary);
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  font-size: 1rem;
  transition: var(--transition);
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}

.password-input {
  position: relative;
}

.password-toggle {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  padding: 0.25rem;
}

.photo-input-container {
  display: flex;
  gap: 1rem;
  align-items: center;
  flex-wrap: wrap;
}

.photo-preview {
  margin-top: 1rem;
  text-align: center;
}

/*  SIMPLIFIED CAMERA STYLES */
#camera-stream {
  width: 100%;
  max-width: 400px;
  height: auto;
  border-radius: 8px;
  margin-top: 1rem;
  border: 3px solid #28a745;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  display: none;
}

#camera-btn {
  background: #007bff;
  color: white;
  border: none;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
}

#camera-btn:hover {
  background: #0056b3;
}

#capture-btn {
  background: #28a745;
  color: white;
  border: none;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  display: none;
  margin-top: 1rem;
}

#capture-btn:hover {
  background: #218838;
}

/*  SIMPLIFIED MAP STYLES */
#location-map {
  height: 300px !important;
  width: 100% !important;
  border: 3px solid #007bff !important;
  border-radius: 12px !important;
  margin: 1rem 0 !important;
  background: #f8f9fa !important;
  overflow: hidden !important;
}

#location-map .leaflet-container {
  height: 100% !important;
  width: 100% !important;
  border-radius: 8px !important;
}

.location-info {
  text-align: center;
  margin-top: 0.5rem;
  padding: 0.5rem;
  background: #e9ecef;
  border-radius: 8px;
  font-weight: bold;
}

.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 2rem;
}

/* View Story Page */
.view-story-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  height: 600px;
}

.stories-map {
  border-radius: var(--border-radius);
  overflow: hidden;
}

.stories-list {
  overflow-y: auto;
  padding: 1rem;
  background: var(--surface-color);
  border-radius: var(--border-radius);
}

.story-item {
  background: var(--background-color);
  border-radius: var(--border-radius);
  padding: 1rem;
  margin-bottom: 1rem;
  box-shadow: var(--shadow);
  cursor: pointer;
  transition: var(--transition);
}

.story-item:hover {
  transform: translateX(4px);
  box-shadow: var(--shadow-lg);
}

.story-item.active {
  border-left: 4px solid var(--primary-color);
}

/* About Page */
.about-content {
  max-width: 800px;
  margin: 0 auto;
}

.about-section h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: var(--primary-color);
}

.about-section ul {
  list-style: none;
  padding-left: 0;
}

.about-section li {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 0.5rem;
  color: var(--text-secondary);
}

.about-section li i {
  color: var(--primary-color);
  width: 20px;
}

/* Modals */
.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  align-items: center;
  justify-content: center;
}

.modal.active {
  display: flex;
}

.modal-content {
  background: var(--background-color);
  border-radius: var(--border-radius);
  max-width: 400px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: var(--shadow-lg);
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem;
  border-bottom: 1px solid var(--border-color);
}

.modal-header h3 {
  margin: 0;
  color: var(--text-primary);
}

.modal-close {
  background: none;
  border: none;
  font-size: 1.25rem;
  color: var(--text-secondary);
  cursor: pointer;
  padding: 0.25rem;
}

.auth-form {
  padding: 1.5rem;
}

/* Toast Notifications */
.toast-container {
  position: fixed;
  top: 80px;
  right: 1rem;
  z-index: 1100;
}

.toast {
  background: var(--background-color);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-lg);
  padding: 1rem 1.5rem;
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  min-width: 300px;
  animation: slideIn 0.3s ease;
}

.toast.success {
  border-left: 4px solid var(--success-color);
}

.toast.error {
  border-left: 4px solid var(--error-color);
}

.toast.warning {
  border-left: 4px solid var(--warning-color);
}

.toast.info {
  border-left: 4px solid var(--primary-color);
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideOut {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(100%);
    opacity: 0;
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .nav-menu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: var(--background-color);
    flex-direction: column;
    padding: 1rem;
    box-shadow: var(--shadow);
    gap: 0;
  }

  .nav-menu.active {
    display: flex;
    animation: slideDown 0.3s ease;
  }

  .nav-menu li {
    width: 100%;
  }

  .nav-link {
    display: block;
    padding: 1rem 0;
    border-bottom: 1px solid var(--border-color);
  }

  .nav-toggle {
    display: block;
  }

  .nav-auth {
    order: -1;
  }

  .page-header h2 {
    font-size: 2rem;
  }

  .stories-grid {
    grid-template-columns: 1fr;
  }

  .view-story-container {
    grid-template-columns: 1fr;
    height: auto;
  }

  .stories-map {
    height: 300px;
  }

  .form-actions {
    flex-direction: column;
  }

  .photo-input-container {
    flex-direction: column;
    align-items: stretch;
  }

  .modal-content {
    width: 95%;
  }

  .pagination {
    flex-wrap: wrap;
  }

  .story-detail-image {
    height: 300px;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 1rem 0.5rem;
  }

  .nav-container {
    padding: 0 0.5rem;
  }

  .page-header h2 {
    font-size: 1.75rem;
  }

  .btn {
    padding: 0.75rem 1rem;
  }

  .toast {
    min-width: auto;
    width: calc(100vw - 2rem);
  }

  .view-story-container {
    gap: 1rem;
  }
}

/* No stories state */
.no-stories {
  grid-column: 1 / -1;
  text-align: center;
  padding: 3rem;
  color: var(--text-secondary);
}

.no-stories h3 {
  margin-bottom: 0.5rem;
  color: var(--text-primary);
}

/* Text alignment helper */
.text-center {
  text-align: center;
}

/* Error message style */
.error-message {
  color: var(--error-color);
  font-weight: 500;
}

/* Focus styles for accessibility */
*:focus {
  outline: 2px solid var(--primary-color);
  outline-offset: 2px;
}

a {
  color: var(--primary-color);
}

a:hover {
  color: var(--primary-hover);
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Header */
.site-header {
  width: 100%;
}

/* Footer */
.site-footer {
  background: var(--surface-color);
  border-top: 1px solid var(--border-color);
  padding: 2rem 1rem;
  text-align: center;
  color: var(--text-secondary);
  font-size: 0.875rem;
}

.site-footer .container {
  max-width: 1200px;
  margin: 0 auto;
}

.site-footer p {
  margin-bottom: 0.5rem;
}

/* View Transitions API Styles */
::view-transition-old(root) {
  animation: fade-out 0.2s ease-out forwards;
}

::view-transition-new(root) {
  animation: fade-in 0.2s ease-in forwards;
}

@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
