ystemFont, \"Segoe UI\", Roboto, sans-serif;\n  line-height: 1.6;\n  color: var(--text-primary);\n  background-color: var(--background-color);\n}\n\n/* Skip Link for Accessibility */\n.skip-link {\n  position: absolute;\n  top: -40px;\n  left: 6px;\n  background: var(--primary-color);\n  color: white;\n  padding: 8px;\n  text-decoration: none;\n  border-radius: var(--border-radius);\n  z-index: 1000;\n}\n\n.skip-link:focus {\n  top: 6px;\n}\n\n/* Navigation */\n.navbar {\n  background: var(--background-color);\n  border-bottom: 1px solid var(--border-color);\n  position: sticky;\n  top: 0;\n  z-index: 100;\n  box-shadow: var(--shadow);\n}\n\n.nav-container {\n  max-width: 1200px;\n  margin: 0 auto;\n  padding: 0 1rem;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  height: 64px;\n}\n\n.nav-brand h1 {\n  color: var(--primary-color);\n  font-size: 1.5rem;\n  font-weight: 700;\n}\n\n.nav-menu {\n  display: flex;\n  list-style: none;\n  gap: 2rem;\n}\n\n.nav-link {\n  text-decoration: none;\n  color: var(--text-secondary);\n  font-weight: 500;\n  transition: var(--transition);\n  padding: 0.5rem 0;\n  border-bottom: 2px solid transparent;\n}\n\n.nav-link:hover,\n.nav-link.active {\n  color: var(--primary-color);\n  border-bottom-color: var(--primary-color);\n}\n\n.nav-auth {\n  display: flex;\n  gap: 1rem;\n  align-items: center;\n}\n\n.nav-toggle {\n  display: none;\n  background: none;\n  border: none;\n  font-size: 1.5rem;\n  color: var(--text-primary);\n  cursor: pointer;\n}\n\n/* Buttons */\n.btn {\n  display: inline-flex;\n  align-items: center;\n  gap: 0.5rem;\n  padding: 0.5rem 1rem;\n  border: none;\n  border-radius: var(--border-radius);\n  font-weight: 500;\n  text-decoration: none;\n  cursor: pointer;\n  transition: var(--transition);\n  font-size: 0.875rem;\n}\n\n.btn-primary {\n  background: var(--primary-color);\n  color: white;\n}\n\n.btn-primary:hover {\n  background: var(--primary-hover);\n}\n\n.btn-outline {\n  backgrou